import React, { FC } from "react";
import clsx from "clsx";

import bombUrl from "assets/mines/bomb.svg";
import hiddenBombUrl from "assets/mines/hidden-bomb.svg";
import diamondUrl from "assets/mines/diamond.svg";
import CircularProgress from "components/CircularProgress";

import styles from "./style.module.scss";

export interface Props {
  isBomb?: boolean;
  isDiamond?: boolean;
  isSelected?: boolean;
  openHidden?: boolean;
  isLoading?: boolean;
  onClick?: () => void;
}

const Tile: FC<Props> = ({
  isBomb,
  isDiamond,
  isSelected,
  openHidden,
  isLoading,
  onClick,
}) => {
  return (
    <div
      draggable={false}
      onClick={onClick}
      className={clsx(
        styles.root,
        { [styles.selected]: isSelected },
        { [styles.hide]: !isBomb && !isDiamond },
        { [styles.overflowHidden]: isDiamond },
        { [styles.highlightTile]: (isBomb || isDiamond) && !openHidden },
        { [styles.openTile]: isBomb || isDiamond },
        { [styles.openHidden]: openHidden },
        { [styles.loading]: isLoading },
      )}>
      {isLoading ? (
        <CircularProgress className={styles.loader} color="#4EC1E7" />
      ) : (
        <div
          className={clsx(
            styles.tile,
            { [styles.open]: isBomb || isDiamond },
            { [styles.explode]: isBomb && !openHidden },
          )}>
          {isBomb && !openHidden ? (
            <img
              draggable={false}
              src={bombUrl}
              alt="bomb"
              className={styles.bomb}
            />
          ) : isBomb && openHidden ? (
            <img
              draggable={false}
              src={hiddenBombUrl}
              alt="hiddenBomb"
              className={styles.hiddenBomb}
            />
          ) : null}
          {isDiamond ? (
            <img
              draggable={false}
              src={diamondUrl}
              alt="diamond"
              className={clsx(styles.diamond, {
                [styles.hiddenDiamond]: openHidden,
              })}
            />
          ) : null}
        </div>
      )}
    </div>
  );
};

export default Tile;
