import React, {
  FunctionComponent,
  useEffect,
  useState,
  useMemo,
  useRef,
  useCallback,
} from "react";
import { useHistory, useLocation } from "react-router-dom";
import clsx from "clsx";

import loseImg from "assets/icons/lose-icon.png";
import winImg from "assets/icons/win-icon.png";
import Share from "./components/Share";

import { useGetLocale } from "providers";

import {
  Button,
  Input,
  GetCoinImg,
  Avatar,
  Icon,
  notifyBarManager,
  CircularProgress,
} from "components";
import { useFetch, useLocalStorage, useModalLocation } from "hooks";
import { letterFormat } from "utils";

import { copy } from "utils/copyToClipboard";

import { useUser } from "providers/User";
import { Dialog } from "containers/PopUps";
import { ModalsProps } from "config/routes";
import { SimpleObject } from "games/Crash/game/engine/types";
import { modalsLoaderColor } from "sass/styleVariables";
import { FetchProfileData } from "../Profile";
import { BetType, Fetch } from "./types";

import { useWindowSize } from "providers";
import { useScreenshot } from "use-react-screenshot";
import css from "./style.module.scss";
import { GameContent } from "./components";
import { Helmet } from "react-helmet";
import * as htmlToImage from "html-to-image";
import { ImageComponent, imageSrcToBase64 } from "./components/ImageComonent";
import html2canvas from "html2canvas";

const avatarStyle = { height: 70, width: 70 };
const buttonWrapper = { background: "transparent", gap: 10 };
const gameLocations: SimpleObject = {
  dice: "hash-dice",
  videopoker: "video-poker",
  blackjack: "blackjack",
};

const mobileScreen = parseInt(css.mobileScreen);

export const BetData: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const intl = useGetLocale();
  const location = useLocation();
  const { width } = useWindowSize();

  const isSm = width <= mobileScreen;

  const {
    params: { id, userName },
    close,
    pushHistoryTo,
  } = useModalLocation("bet-data");

  const [token] = useLocalStorage("token");
  const { user } = useUser();
  const [load, setLoad] = useState(false);
  const [isShareOpen, setIsShareOpen] = useState(false);
  const gameContentRef = useRef<HTMLDivElement | null>(null);
  // const [image, takeScreenShot] = useState("");
  const [image, takeScreenShot] = useState<any>();
  const [coinImg, setCoinImg] = useState(false);

  // const [image, takeScreenShot] = useScreenshot();

  const [betFetch, { data }] = useFetch<Fetch>("statistics/bet");

  const [fetchProfile, { data: profileData }] =
    useFetch<FetchProfileData>("statistics/profile");

  const bet: BetType | null = useMemo(() => {
    if (data && data.bet) {
      return data.bet;
    }

    return null;
  }, [data]);

  const profileAvatar = useMemo(() => {
    const data = profileData;
    if (data && data.profile) {
      return data.profile.profile_picture_url;
    }

    return "";
  }, [profileData]);
  const getImage = useCallback(() => {
    if (gameContentRef.current && coinImg) {
      const imgs = gameContentRef.current.querySelectorAll("img");
      // imgs.forEach((img) => {
      //   img.crossOrigin = "anonymous";
      // });
      // try {
      //   htmlToImage
      //     .toJpeg(gameContentRef.current, {
      //       cacheBust: true,
      //       backgroundColor: "transparent",
      //     })
      //     .then((dataUrl) => {
      //       if (gameContentRef.current)
      //         htmlToImage
      //           .toPng(gameContentRef.current, { cacheBust: true })
      //           .then((dataUrl1) => {
      //             takeScreenShot(dataUrl1);
      //           });
      //     });
      // } catch (error) {
      //   console.log(error);
      // }
      html2canvas(gameContentRef.current, {
        backgroundColor: null,
        useCORS: true,
      }).then((canvas) => {
        try {
          takeScreenShot(canvas.toDataURL());
        } catch (error) {
          console.log(error);
        }
      });
    }
  }, [coinImg]);

  const setCopied = (isCopy: boolean) => {
    if (isCopy) {
      notifyBarManager.success({
        message: "Copied",
        timeOut: 2000,
      });
    } else {
      notifyBarManager.error({
        message: "Error",
        timeOut: 2000,
      });
    }
  };

  const init = () => {
    fetchProfile({
      variables: {
        user_name: userName,
      },
    });
    betFetch({
      variables: {
        bet_id: id,
        language: "en",
      },
    });
  };

  useEffect(() => {
    if (isOpen) {
      init();
    }
  }, [isOpen]);

  const serverRef = useRef<HTMLInputElement>(null);
  const clientRef = useRef<HTMLInputElement>(null);
  const nonceRef = useRef<HTMLInputElement>(null);

  const handleCopyToBuffer = (
    props: any,
    ref: React.RefObject<HTMLInputElement> | null,
  ) => {
    if (props && props.target) {
      const value: string = props.target?.value;
      copy(value, setCopied, ref?.current || undefined);
      props.target.select();
    } else if (
      (typeof props === "string" || typeof props === "number") &&
      ref?.current
    ) {
      copy(String(props), setCopied, ref.current);
    } else {
      setCopied(false);
    }
  };

  const handleClose = () => {
    close();
  };

  const revealed = () => {
    if (bet) {
      handleClose();
      pushHistoryTo({ search: { name: "page", value: "fairness" } });
    }
  };

  const verify = () => {
    window.open(`${process.env.REACT_APP_VERIFY_URL}`);
  };

  const orderToRender = bet?.game !== "CRASH" && bet?.game !== "SLOTS";

  //Callbacks:
  const serverInputCopyToBuffer = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>,
  ) => {
    handleCopyToBuffer(event, serverRef);
  };

  const clientInputCopyToBuffer = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>,
  ) => {
    handleCopyToBuffer(event, clientRef);
  };
  const nonceInputCopyToBuffer = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>,
  ) => {
    handleCopyToBuffer(event, nonceRef);
  };

  const nonceButtonCopyToBuffer = () => {
    if (bet) {
      handleCopyToBuffer(bet.nonce, nonceRef);
    }
  };

  const clientButtonCopyToBuffer = () => {
    if (bet) {
      handleCopyToBuffer(bet.client_seed, clientRef);
    }
  };

  const serverButtonCopyToBuffer = () => {
    if (bet) {
      handleCopyToBuffer(
        bet.server_seed
          ? bet.server_seed
          : intl.formatMessage({
              id: "gameTemplate.statistics.yourBetsTable.betData.rotateSSeed",
            }),
        serverRef,
      );
    }
  };

  const handleCloseModal = () => {
    close();
    handleCloseShare();
  };

  const handleOpenShare = () => {
    setIsShareOpen(true);
  };
  const handleCloseShare = () => {
    setIsShareOpen(false);
  };
  const title = "App Title";

  useEffect(() => {
    if (image) {
      console.log(image);
    }
  }, [image]);

  const path = window.location.pathname + window.location.search;

  return (
    <>
      <Helmet defer={false} onChangeClientState={(state) => console.log(state)}>
        <meta property="og:title" content="European Travel Destinations" />
        <meta
          property="og:image"
          content="https://cdn.pasino.com/user_uploads/63b09ed0382e.jpeg"
        />
        <meta
          property="og:url"
          content="https://front.taprewards.io/page/bet-data/9518558409/rbzcbnuihgxwqkk"
        />
        <meta name="twitter:card" content="summary_large_image" />

        <meta
          property="og:description"
          content="Offering tour packages for individuals or groups."
        />
        <meta
          name="twitter:image:alt"
          content="https://cdn.pasino.com/user_uploads/63b09ed0382e.jpeg"
        />
      </Helmet>

      <Dialog
        fullScreen={isSm}
        paper={{
          className: css.paper,
        }}
        isOpen={isOpen}
        onClose={handleCloseModal}>
        <div className={css.titleWrapper}>
          {data && bet && isOpen ? (
            <span className={css.title}>{letterFormat(bet.game)}</span>
          ) : null}
          <span className={css.header}>
            {intl.formatMessage({
              id: "gameTemplate.statistics.yourBetsTable.betData.title",
            })}
          </span>
          <Icon variant="close" onClick={handleCloseModal} fontSize={12} />
        </div>
        {/* <div ref={gameContentRef}> */}
        <div className={css.box}>
          {image && (
            <div className={css.shareImgWrapper}>
              <img src={image} className={css.shareImg}></img>
            </div>
          )}
          {data && bet && isOpen ? (
            <div className={css.scrollContainer} ref={gameContentRef}>
              <div className={css.dataBox}>
                <span>
                  <Avatar
                    userName={userName}
                    style={avatarStyle}
                    avatar={profileAvatar}
                  />
                </span>
                <div className={css.userData}>
                  <span className={css.userName}>{userName}</span>
                  <span>bet id: {id}</span>
                  <span className={css.userDate}>{bet.date}</span>
                </div>
                <div className={css.resultImg}>
                  <img src={Number(bet.win) === 1 ? winImg : loseImg} />
                </div>
              </div>

              <div className={css.data}>
                <div className={css.cell}>
                  <p className={css.text}>
                    {intl.formatMessage({
                      id: "gameTemplate.statistics.yourBetsTable.betData.bet",
                    })}
                    :
                  </p>

                  <div className={css.textWrapper}>
                    <GetCoinImg coin={bet.coin} className={css.coinIcon} />
                    <p className={css.amount}>{bet.bet_amt}</p>
                  </div>
                </div>
                <div className={css.cell}>
                  <p className={css.text}>
                    {intl.formatMessage({
                      id: "gameTemplate.statistics.yourBetsTable.betData.payout",
                    })}
                    :
                  </p>
                  <div className={css.textWrapper}>
                    <p
                      className={`${css.amount} ${
                        Number(bet.win) === 1 ? css.win : css.lose
                      }`}>
                      {bet.payout
                        ? Number(bet.payout).toFixed(2) + "x"
                        : (0).toFixed(2)}
                    </p>
                  </div>
                </div>
                <div className={css.cell}>
                  <p className={css.text}>
                    {intl.formatMessage({
                      id: "gameTemplate.statistics.yourBetsTable.betData.profit",
                    })}
                    :
                  </p>
                  <div className={css.textWrapper}>
                    <GetCoinImg
                      coin={bet.coin}
                      className={css.coinIcon}
                      onLoad={() => setCoinImg(true)}
                    />
                    <p
                      className={`${css.amount} ${
                        Number(bet.win) === 1 ? css.win : css.lose
                      }`}>
                      {bet.profit}
                    </p>
                  </div>
                </div>
              </div>

              {orderToRender && (
                <div className={css.inpBox}>
                  {(userName ? userName === user?.user_name : true) && (
                    <div className={css.boxItem}>
                      <span>
                        {intl.formatMessage({
                          id: "gameTemplate.statistics.yourBetsTable.betData.serverSeed",
                        })}
                      </span>
                      <div className={css.simpleBox}>
                        <div className={css.inputWrapper}>
                          <Input
                            ref={serverRef}
                            onClick={serverInputCopyToBuffer}
                            readOnly
                            value={
                              bet.server_seed
                                ? bet.server_seed
                                : intl.formatMessage({
                                    id: "gameTemplate.statistics.yourBetsTable.betData.rotateSSeed",
                                  })
                            }
                            className={css.input}
                          />
                          <Button
                            icon
                            className={css.icon}
                            onClick={serverButtonCopyToBuffer}>
                            <Icon variant="copyBetData" withoutButton />
                          </Button>
                        </div>
                      </div>
                    </div>
                  )}
                  <div className={css.clientContent}>
                    {bet.client_seed && (
                      <div className={css.boxItem}>
                        <span>
                          {intl.formatMessage({
                            id: "gameTemplate.statistics.yourBetsTable.betData.clientSeed",
                          })}
                        </span>
                        <div className={css.simpleBox}>
                          <div className={css.inputWrapper}>
                            <Input
                              readOnly
                              ref={clientRef}
                              onClick={clientInputCopyToBuffer}
                              value={bet.client_seed}
                              className={css.input}
                            />
                            <Button
                              icon
                              className={css.icon}
                              onClick={clientButtonCopyToBuffer}>
                              <Icon variant="copyBetData" withoutButton />
                            </Button>
                          </div>
                        </div>
                      </div>
                    )}

                    {bet.nonce && (
                      <div className={css.boxItem}>
                        <span>
                          {intl.formatMessage({
                            id: "gameTemplate.statistics.yourBetsTable.betData.nonce",
                          })}
                        </span>
                        <div className={css.simpleBox}>
                          <div className={css.inputWrapper}>
                            <Input
                              ref={nonceRef}
                              onClick={nonceInputCopyToBuffer}
                              readOnly
                              value={bet.nonce}
                              className={clsx(css.input, css.nonceInput)}
                            />
                            <Button
                              className={clsx(css.icon, css.nonceIcon)}
                              icon
                              onClick={nonceButtonCopyToBuffer}>
                              <Icon variant="copyBetData" withoutButton />
                            </Button>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              )}
              {isShareOpen ? (
                <Share
                  handleCopyToBuffer={handleCopyToBuffer}
                  onClose={handleCloseShare}
                  shareLink={`https://pasino.com${path}`}
                  shareTitle={intl
                    .formatMessage({
                      id: "gameTemplate.statistics.yourBetsTable.share.shareMessage",
                    })
                    .replace("[x]", `${bet?.profit} ${bet?.coin}`)}
                />
              ) : null}

              {(userName ? userName === user?.user_name : true) &&
                bet?.revealed === 0 &&
                orderToRender &&
                token && (
                  <>
                    <div className={css.data} style={buttonWrapper}>
                      <Button onClick={revealed} className={css.textLink}>
                        {intl.formatMessage({
                          id: "gameTemplate.statistics.yourBetsTable.betData.revealedRotate",
                        })}
                      </Button>
                      <Button onClick={verify} className={css.textLink}>
                        {intl.formatMessage({
                          id: "gameTemplate.statistics.yourBetsTable.betData.verify",
                        })}
                      </Button>
                    </div>
                  </>
                )}

              {(userName ? userName === user?.user_name : true) && (
                <div className={css.shareContainer}>
                  <div className={css.share} onClick={handleOpenShare}>
                    <Icon
                      variant="share"
                      className={css.shareImg}
                      withoutButton
                      disabled={!image}
                    />
                    <span>
                      {intl.formatMessage({
                        id: "gameTemplate.statistics.yourBetsTable.share.share",
                      })}
                    </span>
                  </div>
                </div>
              )}
              {bet && (
                <GameContent
                  bet={bet}
                  // rootRef={gameContentRef}
                  onLoadCallback={getImage}
                />
              )}
            </div>
          ) : (
            <div className={css.progressWrapper}>
              <CircularProgress color={modalsLoaderColor} size={70} />
            </div>
          )}
        </div>
        {/* </div> */}
      </Dialog>
    </>
  );
};

export default BetData;
