import React, { FC, useLayoutEffect, useMemo, useState } from "react";

import Row from "games/Diamonds/Combinations/Row";

import { Field, Position } from "games/Diamonds";
import { combinations, paintRow } from "games/Diamonds/Combinations/config";
import { isNumber } from "utils";
import { IDiamondsResult } from "../types";
import { calculateDiamonds } from "../utils/calculateDiamonds";

import css from "./style.module.scss";

export interface IDiamondsComponentProps extends IDiamondsResult {}
const combinationList = combinations;
export const DiamondsComponent: FC<IDiamondsComponentProps> = ({
  outcome,
  roll_numbers,
}) => {
  const repeatDiamonds = new Map();
  const [reloaded, setReloaded] = useState(false);

  const rowComponent = useMemo(() => {
    const { ratio, chance, type } = combinationList[outcome];
    const calculates = calculateDiamonds(roll_numbers);
    console.log(calculates[0]);

    let config = paintRow(calculates[0], calculates[1], type) || [];

    return (
      <Row
        isSelect={false}
        onMouseEnter={() => {}}
        key={`Diamonds_Combinations_${ratio}`}
        diamondsConfig={config}
        ratio={ratio}
        chance={chance}
        profitAmount={"0"}
        classes={{ root: css.rowRoot, combinations: css.combinations }}
      />
    );
  }, []);

  useLayoutEffect(() => {
    setReloaded(true);
  }, []);
  return (
    <div className={css.root}>
      {rowComponent}
      <Field
        fieldWidthProps={1100}
        classes={{ field: `${css.fieldRoot} ${reloaded ? css.visible : ""}` }}>
        {roll_numbers.map((diamondIndex) => {
          repeatDiamonds.set(diamondIndex, diamondIndex);
          return (
            <Position
              key={`position_${Math.random()}`}
              diamondIndex={diamondIndex}
              isWinCombination={
                isNumber(diamondIndex)
                  ? (repeatDiamonds.get(diamondIndex) || 0) > 1
                  : false
              }
            />
          );
        })}
      </Field>
    </div>
  );
};
