import React, { FC, useLayoutEffect, useState } from "react";

import clsx from "clsx";

import { InputAdornment } from "components";
import { calcChance } from "pages/Limbo/controller/manager/utils";
import { LimboInput } from "pages/Limbo/Game/Bet/components";
import { useGetLocale } from "providers";
import { ILimboResult } from "../types";
import { RocketComponent } from "./components/Rocket";

import css from "./style.module.scss";
import { LimboInputComponent } from "./components/InputComponent";

interface IProps extends ILimboResult {
  onLoadCallback: () => void;
}
export const LimboComponent: FC<IProps> = ({
  actual_payout,
  target_payout,
  onLoadCallback,
}) => {
  const [load, setLoad] = useState(false);
  useLayoutEffect(() => {
    if (!load) {
      setLoad(true);
    }
    if (load) {
      setTimeout(() => {
        onLoadCallback();
      }, 500);
    }
  }, [onLoadCallback, load]);

  const intl = useGetLocale();
  return (
    <div className={css.root}>
      <div className={css.labels}>
        <span
          className={clsx(
            css.label,
            actual_payout > target_payout ? css.green : css.red,
          )}>
          {actual_payout}x
        </span>
      </div>
      <RocketComponent />
      <div className={css.footer}>
        <div className={css.inputs}>
          <LimboInputComponent
            readOnly
            title={intl.formatMessage({
              id: "limboInterface.controller.payout",
            })}
            value={target_payout}
          />

          <LimboInputComponent
            readOnly
            endAdornment={<>%</>}
            title={intl.formatMessage({
              id: "limboInterface.controller.chance",
            })}
            value={calcChance(+target_payout)}
          />
        </div>
      </div>
    </div>
  );
};
