import React, { FC, useReducer } from "react";

import Context from "./context";
import {
  initialVideoPokerControlState,
  videoPokerControlReducer,
} from "./reducer";

const VideoPokerControlProvider: FC = ({ children }) => {
  const [controlState, controlDispatch] = useReducer(
    videoPokerControlReducer,
    initialVideoPokerControlState,
  );

  return (
    <Context.Provider
      value={{
        controlState,
        controlDispatch,
      }}>
      {children}
    </Context.Provider>
  );
};

export default VideoPokerControlProvider;
