import { Outcomes } from "games/VideoPoker";

import { removeHoldCard, holdCard, defineCombination } from "./actions";
import * as actions from "./actions/actionTypes";
import { GamePhases } from "./config";
import {
  Hold,
  VideoPokerControlState,
  VideoPokerControlActions,
} from "./types";

const FIELD_SIZE = 5;

const emptyHoldCards: Hold = new Array(FIELD_SIZE).fill("empty");

export const initialVideoPokerControlState: VideoPokerControlState = {
  field: [0, 0, 0, 0, 0],
  holdCards: [...emptyHoldCards],
  gamePhase: GamePhases.NOT_START,
  betAmount: "0.000000",
  outcome: Outcomes.NOTHING,
  combination: [],
  actualBet: "",
  coinsConfig: [],
};

export const videoPokerControlReducer = (
  state: VideoPokerControlState,
  action: VideoPokerControlActions,
): VideoPokerControlState => {
  switch (action.type) {
    case actions.START_GAME:
      return {
        ...state,
        combination: [],
        holdCards: [...emptyHoldCards],
        outcome: Outcomes.NOTHING,
        field: action.cards,
        gamePhase: GamePhases.HOLD,
      };

    case actions.DEAL:
      console.log(action.cards);
      console.log(action.outcome);

      return {
        ...state,
        field: action.cards,
        outcome: action.outcome,
        gamePhase: GamePhases.END_GAME,
        combination: defineCombination(action.cards, action.outcome),
      };

    case actions.SET_GAME_SESSION:
      return {
        ...state,
        field: action.cards,
        gamePhase: GamePhases.HOLD,
        betAmount: action.betAmount,
        actualBet: action.betAmount,
      };

    case actions.SET_FIELD:
      return {
        ...state,
        field: action.cards,
      };

    case actions.ADD_HOLD_CARD:
      return {
        ...state,
        holdCards: holdCard(state.holdCards, action.position, action.card),
      };

    case actions.REMOVE_HOLD_CARD:
      return {
        ...state,
        holdCards: removeHoldCard(state.holdCards, action.position),
      };

    case actions.SET_GAME_PHASE:
      return {
        ...state,
        gamePhase: action.phase,
      };

    case actions.SET_BET_AMOUNT:
      return {
        ...state,
        betAmount: action.amount,
      };

    case actions.SET_OUTCOME:
      return {
        ...state,
        outcome: action.outcome,
      };

    case actions.CLEAR_HOLD_CARDS:
      return {
        ...state,
        holdCards: [...emptyHoldCards],
      };

    case actions.SET_COMBINATION:
      return {
        ...state,
        combination: defineCombination(action.cards, action.outcome),
      };

    case actions.CLEAR_COMBINATION:
      return {
        ...state,
        combination: [],
      };

    case actions.SET_ACTUAL_BET:
      return {
        ...state,
        actualBet: action.bet,
      };

    case actions.SET_COINS_CONFIG:
      return {
        ...state,
        coinsConfig: action.config,
      };

    default:
      return state;
  }
};
