import React, { lazy, Suspense } from "react";
import { createMinesProps } from "./utils/createMinesProps";

import minesSrc from "assets/betData/mines.webp";
import limboSrc from "assets/betData/limbo.webp";
import diamondsSrc from "assets/betData/diamonds.webp";
import rouletteSrc from "assets/betData/roulette.webp";
import blackjackSrc from "assets/betData/blackjack.webp";
import pokerSrc from "assets/betData/poker.webp";
import kenoSrc from "assets/betData/keno.webp";
import fortuneSrc from "assets/betData/fortune.webp";
import crashSrc from "assets/betData/crash.webp";
import diceSrc from "assets/betData/dice.webp";
import plinkoSrc from "assets/betData/plinko.webp";
import towersSrc from "assets/betData/towers.svg";

import { BetType } from "../../types";
import { IMinesResult } from "./types";

const MinesComponent = lazy(() =>
  import("./MinesComponent").then((module) => ({
    default: module.MinesComponent,
  })),
);
const LimboComponent = lazy(() =>
  import("./LimboComponent").then((module) => ({
    default: module.LimboComponent,
  })),
);
const DiamondsComponent = lazy(() =>
  import("./DiamondsComponent").then((module) => ({
    default: module.DiamondsComponent,
  })),
);
const RouletteComponent = lazy(() =>
  import("./RouletteComponent").then((module) => ({
    default: module.RouletteComponent,
  })),
);
const BlackjackComponent = lazy(() =>
  import("./BlackjackComponent").then((module) => ({
    default: module.BlackjackComponent,
  })),
);
const PokerComponent = lazy(() =>
  import("./PokerComponent").then((module) => ({
    default: module.PokerComponent,
  })),
);
const KenoComponent = lazy(() =>
  import("./KenoComponent").then((module) => ({
    default: module.KenoComponent,
  })),
);
const FortuneComponent = lazy(() =>
  import("./FortuneComponent").then((module) => ({
    default: module.FortuneComponent,
  })),
);
const CrashComponent = lazy(() =>
  import("./CrashComponent").then((module) => ({
    default: module.CrashComponent,
  })),
);
const DiceComponent = lazy(() =>
  import("./DiceComponent").then((module) => ({
    default: module.DiceComponent,
  })),
);
const PlinkoComponent = lazy(() =>
  import("./PlinkoComponent").then((module) => ({
    default: module.PlinkoComponent,
  })),
);
const TowersComponent = lazy(() =>
  import("./TowersComponent").then((module) => ({
    default: module.TowersComponent,
  })),
);
interface IDataGameList {
  [key: string]: {
    id: number;
    img: string;
    pagePath: string;
    descriptionLocale?: string;

    renderComponent: (
      props: BetType,
      onLoadCallback?: () => void,
    ) => React.ReactElement<any, any>;
  };
}
export const dataGameList: IDataGameList = {
  MINES: {
    id: 0,
    img: minesSrc,
    pagePath: "/mines",
    renderComponent: (props: { game_result: IMinesResult }) => {
      const createdProps = createMinesProps(props.game_result);

      return (
        <Suspense fallback={null}>
          <MinesComponent fieldProps={createdProps} />
        </Suspense>
      );
    },
  },
  LIMBO: {
    id: 1,
    img: limboSrc,
    pagePath: "/limbo",

    renderComponent: (props, callback) => {
      return (
        <Suspense fallback={null}>
          <LimboComponent {...props.game_result} onLoadCallback={callback} />
        </Suspense>
      );
    },
  },
  DIAMONDS: {
    id: 2,
    img: diamondsSrc,
    pagePath: "/diamonds",
    renderComponent: (props) => {
      return (
        <Suspense fallback={null}>
          <DiamondsComponent {...props.game_result} />
        </Suspense>
      );
    },
  },
  ROULETTE: {
    id: 3,
    img: rouletteSrc,
    pagePath: "/roulette",
    renderComponent: (props) => {
      return (
        <Suspense fallback={null}>
          <RouletteComponent {...props.game_result} />
        </Suspense>
      );
    },
  },
  BLACKJACK: {
    id: 4,
    img: blackjackSrc,
    pagePath: "/blackjack",
    renderComponent: (props) => {
      const { game_result, client_seed } = props;
      return (
        <Suspense fallback={null}>
          <BlackjackComponent {...game_result} seed={client_seed} />
        </Suspense>
      );
    },
  },
  VIDEOPOKER: {
    id: 5,
    img: pokerSrc,
    pagePath: "/video-poker",
    renderComponent: (props) => {
      return (
        <Suspense fallback={null}>
          <PokerComponent
            {...props.game_result}
            bet_amt={props.bet_amt}
            coin={props.coin}
          />
        </Suspense>
      );
    },
  },
  KENO: {
    id: 6,
    img: kenoSrc,
    pagePath: "/keno",
    renderComponent: (props) => {
      return (
        <Suspense fallback={null}>
          <KenoComponent
            {...props.game_result}
            bet_amnt={props.bet_amt}
            coin={props.coin}
          />
        </Suspense>
      );
    },
  },
  WHEEL: {
    id: 7,
    img: fortuneSrc,
    pagePath: "/fortune-quest",
    renderComponent: (props: any) => {
      return (
        <Suspense fallback={null}>
          <FortuneComponent {...props.game_result} />
        </Suspense>
      );
    },
  },
  CRASH: {
    id: 7,
    img: crashSrc,
    pagePath: "/crash",
    renderComponent: (props) => {
      return (
        <Suspense fallback={null}>
          <CrashComponent {...props.game_result} />
        </Suspense>
      );
    },
  },
  DICE: {
    id: 8,
    img: diceSrc,
    pagePath: "/hash-dice",
    renderComponent: (props, callback) => {
      return (
        <Suspense fallback={null}>
          <DiceComponent
            {...props.game_result}
            win={props.win}
            onLoadCallback={callback}
          />
        </Suspense>
      );
    },
  },
  PLINKO: {
    id: 9,
    img: plinkoSrc,
    pagePath: "/plinko",
    renderComponent: (props) => {
      return (
        <Suspense fallback={null}>
          <PlinkoComponent {...props.game_result} />
        </Suspense>
      );
    },
  },
  TOWERS: {
    id: 10,
    img: towersSrc,
    pagePath: "/towers",
    renderComponent: (props) => {
      return (
        <Suspense fallback={null}>
          <TowersComponent
            {...props.game_result}
            profit={props.profit}
            coin={props.coin}
            win={props.win}
            payout={props.payout}
          />
        </Suspense>
      );
    },
  },
};
