import { Button } from "components";
import { useGetLocale } from "providers";
import React, { FC } from "react";
import { useHistory } from "react-router-dom";
import css from "./style.module.scss";
interface IProps {
  game: string;
  descriptionLocale?: string;
  gameImg: string;
  path: string;
}
export const PlayNow: FC<IProps> = ({
  descriptionLocale,
  game,
  gameImg,
  path,
}) => {
  const intl = useGetLocale();
  const { push } = useHistory();
  return (
    <div className={css.root}>
      <div className={css.gameInfo}>
        <img className={css.gameImg} src={gameImg} alt="game_img" />
        <div className={css.info}>
          <h4>{game.toUpperCase()}</h4>
          <span>
            {descriptionLocale
              ? intl.formatMessage({ id: descriptionLocale })
              : "Original games"}
          </span>
        </div>
      </div>
      <Button className={css.playButton} onClick={() => push(path)}>
        Play Now
      </Button>
    </div>
  );
};
