import { Dispatch, createContext } from "react";

import { initialVideoPokerControlState } from "./reducer";
import { VideoPokerControlActions, VideoPokerControlState } from "./types";

type VideoPokerControlContext = {
  controlState: VideoPokerControlState;
  controlDispatch: Dispatch<VideoPokerControlActions>;
};

export const defaultValue: VideoPokerControlContext = {
  controlState: initialVideoPokerControlState,
  controlDispatch: () => {},
};

const Context = createContext<VideoPokerControlContext>(defaultValue);

export default Context;
