import { get as lodashGet } from "utils/lodash";

import { isNumber } from "utils";
import { Outcomes, CardIndex, CardValue, deck } from "games/VideoPoker";

import { CardPosition } from "../types";

const fullPositions: Array<CardPosition> = [0, 1, 2, 3, 4];
const getFullPositions = () => [...fullPositions];

type SameCardsCounting = {
  [key in CardValue]: number;
};

const findSameCards = (field: Array<CardIndex>, outcome: Outcomes) => {
  const sameCardsCounting = {} as SameCardsCounting;
  field.forEach((cardIndex) => {
    sameCardsCounting[deck.getCard(cardIndex).cardValue] =
      lodashGet(sameCardsCounting, deck.getCard(cardIndex).cardValue, 0) + 1;
  });

  const isPairOfJacksOrBetter = outcome === Outcomes.PAIR_OF_JACKS_OR_BETTER;
  const cards: Array<CardPosition> = [];
  field.forEach((cardIndex, position) => {
    const skipLowPairs =
      isNumber(deck.getCard(cardIndex).cardValue) && isPairOfJacksOrBetter;
    if (
      sameCardsCounting[deck.getCard(cardIndex).cardValue] > 1 &&
      !skipLowPairs
    ) {
      cards.push(position as CardPosition);
    }
  });

  return cards;
};

const combinations: Record<
  Outcomes,
  (field: Array<CardIndex>, outcome: Outcomes) => Array<CardPosition>
> = {
  [Outcomes.ROYAL_FLUSH]: getFullPositions,
  [Outcomes.STRAIGHT_FLUSH]: getFullPositions,
  [Outcomes.FLUSH]: getFullPositions,
  [Outcomes.FOUR_OF_A_KIND]: findSameCards,
  [Outcomes.FULL_HOUSE]: getFullPositions,
  [Outcomes.STRAIGHT]: getFullPositions,
  [Outcomes.THREE_OF_A_KIND]: findSameCards,
  [Outcomes.TWO_PAIR]: findSameCards,
  [Outcomes.PAIR_OF_JACKS_OR_BETTER]: findSameCards,
  [Outcomes.NOTHING]: () => [],
};

export const defineCombination = (field: Array<CardIndex>, outcome: Outcomes) =>
  combinations[outcome](field, outcome);
