import React, { FC, useEffect, useRef } from "react";

import { BetType } from "../../types";
import { dataGameList } from "../Games/gameConfig";
import { PlayNow } from "../PlayNow";

import css from "./style.module.scss";

interface IProps {
  bet: BetType;
  // rootRef: React.LegacyRef<HTMLDivElement>;
  onLoadCallback?: () => void;
}

export const GameContent: FC<IProps> = ({ bet, onLoadCallback }) => {
  if (!bet.game || bet.game === "SLOTS") return null;
  const game = dataGameList[bet.game];
  if (!game || bet.game_result === null || !bet.game_result) return null;

  const { img, renderComponent, descriptionLocale, pagePath } = game;
  return (
    <div className={css.root}>
      <div className={css.gameSection}>
        {renderComponent(bet, onLoadCallback)}
      </div>

      <PlayNow
        game={bet.game}
        gameImg={img}
        descriptionLocale={descriptionLocale}
        path={pagePath}
      />
    </div>
  );
};
