import React, { FC } from "react";

import Tile, { Props } from "games/Mines/Field/Tile";
import { field } from "games/Mines/Field/config";

import css from "./style.module.scss";

export interface IMinesComponentProps {
  fieldProps: Record<number, Props | null>;
}
export const MinesComponent: FC<IMinesComponentProps> = ({ fieldProps }) => {
  const tiles = field.map((tileIndex) => {
    const key = `bet_mines_field_${tileIndex}`;
    return <Tile key={key} {...fieldProps[tileIndex]} />;
  });
  return (
    <div className={css.root}>
      <div className={css.field}>{tiles}</div>
    </div>
  );
};
