import React, { FC, useMemo } from "react";

import { CardFront, SetOfCards } from "games/VideoPoker";
import {
  CardPosition,
  defineCombination,
} from "pages/VideoPoker/Controller/components/Reducer";
import { IPokerResult } from "../types";

import css from "./style.module.scss";

export interface IPokerComponentProps extends IPokerResult {
  bet_amt: string;
  coin: string;
}
export const PokerComponent: FC<IPokerComponentProps> = ({
  final_hand,
  hold,
  outcome,
  bet_amt,
  coin,
}) => {
  const hand = useMemo(() => {
    return final_hand.map((cardIndex, index) => {
      const cardIsHold = hold.includes(cardIndex);
      const isCombination = defineCombination(
        final_hand.map((v) => +v),
        outcome as any,
      ).includes(index as CardPosition);
      return (
        <CardFront
          classes={{
            root: css.card,
          }}
          cardIndex={+cardIndex}
          onClick={() => {}}
          isSelect={cardIsHold}
          isCombination={isCombination}
          isLose={!isCombination}
        />
      );
    });
  }, []);
  return (
    <div className={css.root}>
      <SetOfCards
        outcome={outcome as any}
        coinAmount={bet_amt}
        coin={coin || ""}
        USDMode={false}
        classes={{
          root: css.setCardRoot,
        }}
      />
      <div className={css.field}>{hand}</div>
    </div>
  );
};
