import { IMinesResult } from "./../types";
import { FIELD_SIZE } from "games/Mines/Field/config";
import { Props as TileProps } from "games/Mines/Field/Tile";

export const createMinesProps = (result: IMinesResult) => {
  const { sweeped_tiles, mine_locations, status } = result;
  const props: Record<number, TileProps | null> = {};

  for (let i = 0; i < FIELD_SIZE; i++) {
    if (sweeped_tiles.includes(String(i))) {
      props[i] = { isDiamond: true };
      if (mine_locations.includes(String(i))) {
        props[i] = { isBomb: true };
      }
    } else {
      props[i] = { isDiamond: true, openHidden: true };
      if (mine_locations.includes(String(i))) {
        props[i] = { isBomb: true, openHidden: true };
      }
    }
  }
  return props;
};
