import { range } from "utils/lodash";

import { Field } from "./types";
import { Props as TileProps } from "./Tile";

export const FIELD_SIZE = 25;

export const field = Object.freeze(range(FIELD_SIZE));

export const fieldProps = field.reduce((accumulator, tileIndex) => {
  accumulator[tileIndex] = null;
  return accumulator;
}, {} as Field);

export const getFieldProps = (tileProps: TileProps | null) =>
  field.reduce((accumulator, tileIndex) => {
    accumulator[tileIndex] = tileProps ? { ...tileProps } : tileProps;
    return accumulator;
  }, {} as Field);

export const getTilesProps = (
  tilesIndexes: Array<number>,
  tileProps: TileProps | null,
) =>
  tilesIndexes.reduce((accumulator, tileIndex) => {
    accumulator[tileIndex] = { ...tileProps };
    return accumulator;
  }, {} as Record<number, TileProps | null>);
