import React, { FC } from "react";

import css from "./style.module.scss";
interface IProps
  extends React.DetailedHTMLProps<
    React.InputHTMLAttributes<HTMLInputElement>,
    HTMLInputElement
  > {
  title?: string;
  endAdornment?: JSX.Element | null | undefined;
}
export const LimboInputComponent: FC<IProps> = ({
  endAdornment,
  title,
  ...props
}) => {
  return (
    <div className={css.root}>
      {title ? <h4 className={css.title}>{title}</h4> : null}
      <input className={css.input} {...props} />

      {endAdornment ? (
        <div className={css.adronmentWrapper}>{endAdornment}</div>
      ) : null}
    </div>
  );
};
