import { DiamondIndex } from "games/Diamonds";

const get = (map: Map<any, any>, key: any, defaultValue: any) => {
  const value = map.get(key);
  return value === undefined ? defaultValue : value;
};

export const calculateDiamonds = (field: Array<DiamondIndex>) => {
  const diamondsCount = new Map() as Map<DiamondIndex, number>;

  for (const diamondIndex of field) {
    diamondsCount.set(diamondIndex, get(diamondsCount, diamondIndex, 0) + 1);
  }

  const repeatedDiamonds = [] as Array<DiamondIndex>;
  diamondsCount.forEach((count, diamondIndex) => {
    if (count > 2 && repeatedDiamonds.length > 0) {
      repeatedDiamonds[1] = repeatedDiamonds[0];
      repeatedDiamonds[0] = diamondIndex;
    } else if (count > 1) {
      repeatedDiamonds.push(diamondIndex);
    }
  });

  if (repeatedDiamonds.length === 0) {
    repeatedDiamonds.push(field[0], field[1]);
  } else if (repeatedDiamonds.length === 1) {
    const singleDiamond =
      field.find((diamondIndex) => diamondIndex !== repeatedDiamonds[0]) || 0;
    repeatedDiamonds.push(singleDiamond);
  }

  return repeatedDiamonds;
};
