import { CardIndex } from "games/VideoPoker";
import { Hold } from "../types";
import { CardPosition } from "../types";

export const removeHoldCard = (holdCards: Hold, position: CardPosition) => {
  const holdCardsCopy = [...holdCards];
  holdCardsCopy[position] = "empty";
  return holdCardsCopy;
};

export const holdCard = (
  holdCards: Hold,
  position: CardPosition,
  card: CardIndex,
) => {
  const holdCardsCopy = [...holdCards];
  holdCardsCopy[position] = card;
  return holdCardsCopy;
};
